import React from "react";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import CardContent from "../../utils/constants/Courses"
import CoursesContent from "../content/courses";

const useStyles = makeStyles((theme) => ({
    divider: {
        width: '6%',
        borderTop: '4px solid #00bdff',
        borderRadius: '5px',
    },
    coursesContainer: {
        position: 'relative',
        padding:theme.spacing(5),
    },
}));

const LearningGuideContainer = () => {
    const classes = useStyles()
    return (
        <>
            <Typography variant="h5" align='center' style={{ width: '100%' }}>
                {CardContent[1].heading}
            </Typography>
            <hr className={classes.divider}></hr>
            <Typography variant="h5" align='center' style={{ width: '100%' }}>
                {CardContent[1].subHeading}
            </Typography>
            <Grid
                container
                spacing={5}
                justify="center"
                className={classes.coursesContainer}
            >
                {CardContent[1].content.map((item) => (
                    <Grid className={classes.cardContainer} item xs={12} sm={12} md={6} lg={6} xl={6}>
                        <CoursesContent
                            mainHeading={item.title}
                            subHeading={item.subTitle}
                            link={item.to}
                            image={item.image}
                        />
                    </Grid>
                ))}
            </Grid>
        </>
    );
};

export default LearningGuideContainer;