import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import arrow from '../../assets/icons/arrow.png'

const useStyles = makeStyles((theme) => ({
    card: {
        width: '100%',
        [theme.breakpoints.up("lg")]: {
            width: '80%',
        },
        margin: 'auto',
        height: '100%',
        textAlign: 'center',
        right: '75%',
        transform: 'translateX(75%)',
        border:'1px solid red'
    },
    bullet: {
        display: "inline-block",
        margin: "0 2px",
        transform: "scale(0.8)"
    },
    title: {
        fontSize: 14
    },
    coursesImageContainer: {
        position: 'absolute',
        top: '0',
        width: '60%',
        height: 241,
        [theme.breakpoints.up("lg")]: {
            height: 301,
        },
        border: '3px solid red'
    },
    coursesDetailsContainer: {
        position: 'absolute',
        top: '50%',
        transform: 'translateY(-50%)',
        width: '60%',
        height: 189,
        border: '1px solid red'
    },
    coursesContentContainer: {
        position: 'relative',
        width: '100%',
        height: 241,
        [theme.breakpoints.up("lg")]: {
            height: 301,
        },
        border: '1px solid red'
    },
    knowMoreContainer: {
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        position: 'absolute',
        bottom: 2,
        left: 0,
        right: 0,
        padding: '20px',
        textDecoration: 'none',
        color: '#00BDFF',
    },
    knowMoreIcon: {
        width: '20',
        height: '20',
        marginLeft: '10px'
    }
}));

const CoursesContent = ({ mainHeading, subHeading, link, image }) => {
    const classes = useStyles();
    return (

        <>
            <div className={classes.coursesContentContainer}>
                <img className={classes.coursesImageContainer} alt={`${mainHeading}-logo`} src={image} />
                <div className={classes.coursesDetailsContainer}>
                    <Card className={classes.card}>
                        <CardContent>
                            <Typography variant="h6" align='center'>
                                {mainHeading}
                            </Typography>
                            <Typography variant="body2" color="textSecondary">
                                {subHeading}
                            </Typography>
                            <CardActions>
                                <a
                                    className={classes.knowMoreContainer}
                                    href={link}
                                >
                                    <Typography variant="body1" align='center'>
                                        Learn more
                                    </Typography>
                                    <img className={classes.knowMoreIcon} alt='know more' src={arrow} />
                                </a>
                            </CardActions>
                        </CardContent>
                    </Card>
                </div>
            </div>
            {/* <Card className={classes.root}>
                <CardContent>
                    <Grid container direction="row" alignItems="center">
                        <Grid item>
                            <img className='header-logo' alt={'title'} src={"Sapiens.svg"} style={{ width: "50px", height: '50px', borderRadius: "25px", border: "1px solid black" }} />
                        </Grid>
                        <Grid item>
                            <Typography variant="h5" style={{ marginLeft: "10px" }}>
                                {mainHeading}
                            </Typography>
                        </Grid>
                    </Grid>
                    <Typography variant="h6" className={classes.pos} color="textSecondary">
                        {subHeading}
                    </Typography>
                    {contents.map((item) => (
      <a
        style={{ display: 'flex', textDecoration: 'none' }}
        href={item.to}
      >
        {bull}{item.title}
      </a>
    ))}
                </CardContent>
            </Card> */}
        </>
    );
};

export default CoursesContent;
