import React, { useState } from "react";
import { fade, withStyles } from "@material-ui/core/styles";
import {
    AppBar,
    Toolbar,
    IconButton,
    InputBase,
    MenuItem,
    Menu
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import MoreIcon from "@material-ui/icons/MoreVert";
import CardContent from "../../utils/constants/Courses";
import logo from '../../assets/Sapiens.svg'

const styles = (theme) => ({
    grow: {
        flexGrow: 1
    },
    title: {
        display: "none",
        [theme.breakpoints.up("sm")]: {
            display: "block"
        }
    },
    search: {
        position: "relative",
        border: '1px solid #00278b',
        borderRadius: '25px',
        marginRight: theme.spacing(7),
        marginLeft: 0,
        width: "100%",
        [theme.breakpoints.up("sm")]: {
            marginLeft: theme.spacing(3),
            width: "auto"
        },
        [theme.breakpoints.down("md")]: {
            display:'none'
        }
    },
    searchIcon: {
        width: theme.spacing(7),
        height: "100%",
        position: "absolute",
        right: 0,
        pointerEvents: "none",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        color: "#00278b",
    },
    inputRoot: {
        color: "black"
    },
    inputInput: {
        padding: theme.spacing(1, 7, 1, 2),
        transition: theme.transitions.create("width"),
        width: "100%",
        [theme.breakpoints.up("md")]: {
            width: 200
        }
    },
    sectionDesktop: {
        display: "none",
        [theme.breakpoints.up("md")]: {
            display: "flex"
        }
    },
    sectionMobile: {
        display: "flex",
        [theme.breakpoints.up("md")]: {
            display: "none"
        }
    },
    navbarLinks: {
        display: 'flex',
        textDecoration: 'none',
        marginLeft: '20px',
        color: '#00287b'
    }
});

const ToolbarComponent = (props) => {
    const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(false);

    const handleMobileMenuOpen = (e) => {
        setMobileMoreAnchorEl(e.currentTarget);
    };

    const handleMobileMenuClose = () => {
        setMobileMoreAnchorEl(null);
    };

    const { classes } = props;

    const mobileMenuId = "primary-search-account-menu-mobile";

    const renderMobileMenu = (
        <Menu
            anchorEl={mobileMoreAnchorEl}
            anchorOrigin={{ vertical: "top", horizontal: "right" }}
            id={mobileMenuId}
            keepMounted
            transformOrigin={{ vertical: "top", horizontal: "right" }}
            open={mobileMoreAnchorEl}
            onClose={handleMobileMenuClose}
        >
            {CardContent.map((item) => (
                <MenuItem>
                    <a
                        className={classes.navbarLinks}
                        href={item.to}
                    >
                        {item.heading}
                    </a>
                </MenuItem>
            ))}
        </Menu>
    );

    return (
        <div className={classes.grow}>
            <AppBar position="static" style={{ background: "white" }}>
                <Toolbar>
                    <img className='header-logo' alt={'title'} src={logo} />
                    <div className={classes.grow} />
                    <div className={classes.sectionDesktop}>
                        {CardContent.map((item) => (
                            <a
                                className={classes.navbarLinks}
                                href={item.to}
                            >
                                {item.heading}
                            </a>
                        ))}
                    </div>
                    <div className={classes.search}>
                        <div className={classes.searchIcon}>
                            <SearchIcon />
                        </div>
                        <InputBase
                            placeholder="Search…"
                            classes={{
                                root: classes.inputRoot,
                                input: classes.inputInput
                            }}
                            inputProps={{ "aria-label": "search" }}
                        />
                    </div>
                    <div className={classes.sectionMobile}>
                        <IconButton
                            aria-label="show more"
                            aria-controls={mobileMenuId}
                            aria-haspopup="true"
                            onClick={handleMobileMenuOpen}
                            color="black"
                        >
                            <MoreIcon />
                        </IconButton>
                    </div>
                </Toolbar>
                {renderMobileMenu}
            </AppBar>
        </div>
    );
};

export default withStyles(styles)(ToolbarComponent);
